// import React from 'react'
import './Home.css';
// import React, { useState, useEffect } from 'react';
// import './CircularMotion.css';
// import im from '../images/flowerbg.png';
import image from '../images/plant.jpg';
import image2 from '../images/plant7.jpg';
import image3 from '../images/cactus.png';
import image4 from '../images/cactus2.png';
import image5 from '../images/potted.png';
import image6 from '../images/houseplant.png';
import imge from '../images/un.jpg';
import imge2 from '../images/un2.jpg';
import imge3 from '../images/un3.jpg';
import imge4 from '../images/un4.jpg';
import imge5 from '../images/un5.jpg';
import imge6 from '../images/un6.jpg';
import imge7 from '../images/un7.jpg';
import imge8 from '../images/un8.jpg';
import imge9 from '../images/un9.jpg';
import imge10 from '../images/un10.jpg';
// import imge11 from '../images/un11.jpg';
// import imge12 from '../images/un12.jpg';
import Slider from 'react-slick';
// import './Slider.css'; // Import your CSS file for styling
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
const Home = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true, // Enable auto sliding
    autoplaySpeed: 3000, // Time between slides in milliseconds (3 seconds in this example)
  };
  return(
    <>
      
   
    <Slider {...settings} className="slider-container ">
      <div className="slider-item">
          <img src={image2} alt="Slide 1" />
        <div className="centered-text">
          <h1>Eco Time</h1>
          <p>"Embracing Nature: A Sustainable Tomorrow"</p>
        </div>
      </div>
      <div className="slider-item">
        <img src={image} alt="Slide 2" />
        <div className="centered-text">
          <h1>Ever Green</h1>
          <p>"Preserving Our Planet: A Call to Action"</p>
        </div>
      </div>
      {/* Add more slider items as needed */}
      </Slider>
      <div className="centered-container-cate">
      <div className="centered-text-cate">
        <h1>Browse our categories</h1>
        <p>Botanical Classification</p>
      </div>
    </div>

      <div className="image-grid container" >
      <div className="image-container">
        <img src={image3} alt="Img 1"  width="100px"/>
        <div className="image-text">
          <p>Cacti (Succulents)</p>
        </div>
      </div>
      <div className="image-container">
        <img src={image4} alt="Img 2" width="100px"/>
        <div className="image-text">
          <p>Mosses (Bryophytes)</p>
        </div>
      </div>
      <div className="image-container">
        <img src={image5} alt="Img 3"  width="100px"/>
        <div className="image-text">
          <p>Ferns (Pteridophytes)</p>
        </div>
      </div>
      <div className="image-container">
        <img src={image6} alt="Img 4" width="100px" />
        <div className="image-text">
          <p>Conifers (Gymnosperms)</p>
        </div>
      </div>
    </div>

<>
  <div className="marquee">
    <div className="marquee--group">
      <img
        src={imge}
        alt="1"
      />
      <img
        src={imge2}
        alt="2"
      />
      <img
        src={imge3}
        alt="3"
      />
      <img
        src={imge4}
        alt="4"
      />
      <img
        src={imge5}
        alt="5"
      />
    </div>
    <div aria-hidden="true" className="marquee--group">
      <img
        src={imge}
        alt="i"
      />
      <img
        src={imge2}
        alt="im"
      />
      <img
        src={imge3}
        alt="ima"
      />
      <img
        src={imge4}
        alt="imag"
      />
      <img
        src={imge5}
        alt="image23"
      />
    </div>
  </div>
  <div className="marquee marquee--borders">
    <div className="marquee--group">
      <p>Nature Eco Time</p>
      <p aria-hidden="true">Nature Eco Time</p>
      <p aria-hidden="true">Nature Eco Time</p>
    </div>
    <div aria-hidden="true" className="marquee--group">
      <p>Nature Eco Time</p>
      <p>Nature Eco Time</p>
      <p>Nature Eco Time</p>
    </div>
  </div>
  <div className="marquee marquee--reverse">
    <div className="marquee--group">
      <img
        src={imge6}
        alt="0"
      />
      <img
        src={imge7}
        alt="9"
      />
      <img
        src={imge8}
        alt="8"
      />
      <img
        src={imge9}
        alt="7"
      />
      <img
        src={imge10}
        alt="6"
      />
    </div>
    <div aria-hidden="true" className="marquee--group">
      <img
        src={imge6}
        alt="11"
      />
      <img
        src={imge7}
        alt="12"
      />
      <img
        src={imge8}
        alt="13"
      />
      <img
        src={imge9}
        alt="14"
      />
      <img
        src={imge10}
        alt="15"
      />
    </div>
  </div>
</>

      

  {/* <div className="circular-motion-container">
      <div className="moving-image">
          <img src={im} alt="Moving Image78" />
        </div>
      </div> */}
      
     
  

  </>
  );
};

export default Home;
